import Vue from 'vue'
import VueRouter from 'vue-router'
// Vue.use() 
Vue.use(VueRouter)

import VueResource from 'vue-resource'
Vue.use(VueResource)

import Vuex from 'vuex'
Vue.use(Vuex)

var store = new Vuex.Store({
	//
	state:{
		name:'xie&lan',
	},
	mutations:{
		//font-end
		set_fe:function(state,data){
			state.name = data
		}
	}
})

/*const Foo = {
	template: '<div>foo</div>'
}*/
var Foo = require('./components/foo.vue');
var Bar = require('./components/bar.vue');
var UserProfile = {
	template:'<p>{{name}}</p>',
	data(){
		return {
			name:''
		}
	},
	mounted(){
		console.log(this)
		console.log(this.$route.params)
		this.$http.jsonp('http://localhost:81/angular/0309/webpack-vue/test.php',{
			params:{
				callback:"JSON_CALLBACK"
			}
		}).then(function(data){
			console.log(data.data)
			this.name = data.data.name
		},function(){
			
		})
	}
}
var UserPosts = {
	template:'<p>UserPosts</p>',
	mounted:function(){
		console.log(this.$route.params)
	}
}
const router = new VueRouter({
	routes: [{
		path: '/foo',
		component: Foo,
		//
		children: [
        {
          //  /user/:id/profile 
          // UserProfile  User  <router-view> 
          path: 'a/:id',
          component: UserProfile
          //controller+template==components
        },
        {
          //  /user/:id/posts 
          // UserPosts  User  <router-view> 
          path: 'b/:id',
          component: UserPosts
        }
      ]
	}, {
		path: '/bar',
		component: Bar
	}]
})

var xfooter = require("./components/xfooter.vue");
//import Vue from "vue";
//
var xheader = Vue.extend({
	template: require("./template/xheader.html")
})
new Vue({
	//el: '#demo',
	router,
	store
}).$mount('#demo')


